@font-face {
  font-family: "Eudoxus Sans";
  src: url("../../../assets/fonts/EudoxusSans-Medium.woff2") format("woff2"),
    url("../../../assets/fonts/EudoxusSans-Medium.woff") format("woff"),
    url("../../../assets/fonts/EudoxusSans-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Eudoxus Sans";
  src: url("../../../assets/fonts/EudoxusSans-Regular.woff2") format("woff2"),
    url("../../../assets/fonts/EudoxusSans-Medium.woff") format("woff"),
    url("../../../assets/fonts/EudoxusSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

// Font Family
$font-display: "Eudoxus Sans";

body {
  font-family: $font-display;
}

